export default {
  name: 'BaseToggleButton',
  props: {
    id: {
      type: String,
      default () {
        return `toggle-input-${this._uid}`
      }
    },
    value: {
      type: [Boolean],
      default: false
    },
    label: {
      type: [String],
      default: null
    },
    error: {
      type: String,
      default: null,
      required: false
    },
    helpText: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      state: this.value
    }
  },
  watch: {
    value () {
      this.state = this.value
    }
  },
  methods: {
    update () {
      this.state = !this.state
      this.$emit('input', this.state)
    }
  }
}
